
import { defineComponent } from "vue";
import CloudwatchRule from "@/components/monitor/lambda/model/CloudwatchRule";
import LambdaInfo from "@/components/monitor/lambda/model/LambdaInfo";
import MonitorLambdaCarousel from "@/components/monitor/lambda/MonitorLambdaCarousel.vue";
import axios from "axios";
import LambdaEventRule from "@/components/monitor/lambda/model/LambdaEventRule";

export default defineComponent ({
  name: "MonitorLambdaCards",

  components: {
    MonitorLambdaCarousel
  },

  props: {
  },

  data() {
    return {
      lambda_rules: null as null | Map<string, LambdaEventRule>,
      lambda_infos: null as null | Map<string, LambdaInfo[]>
    }
  },

  async created() {
    this.get_lambda_event_rules()
    this.get_lambda_infos()
  },

  methods: {
    get_lambda_event_rules() {
      axios.get('http://127.0.0.1:8082/api/monitor/appium-lambda/event/rules', {})
          .then(res => {
            const res_lambda_rules = new Map<string, LambdaEventRule>();
            res.data.lambda_rules.forEach((rule: LambdaEventRule) => res_lambda_rules.set(rule.rule.name, rule));
            this.lambda_rules = res_lambda_rules;
          })
          .catch(error => (console.log(error)))
    },

    put_update_state_lambda_event_rule(lambda_rule: CloudwatchRule, state: string) {
      const prev_state = lambda_rule.state
      lambda_rule.state = null
      const updated_lambda_rule = Object.assign({}, lambda_rule)
      updated_lambda_rule.state = state

      axios.put('http://127.0.0.1:8082/api/monitor/appium-lambda/event/rule', updated_lambda_rule)
          .then(res => {lambda_rule.state = state})
          .catch(error => {console.log(error); lambda_rule.state = prev_state;})
    },

    get_lambda_infos() {
      axios.get('http://127.0.0.1:8082/api/monitor/appium-lambda/infos', {})
          .then(res => {this.lambda_infos = res.data})
          .catch(error => {console.log(error)})
    }
  }
})
