
import {defineComponent, PropType} from "vue";
import LambdaInfo from "@/components/monitor/lambda/model/LambdaInfo";

export default defineComponent({
  name: "MonitorLambdaCarousel",
  props: {
    lambdaInfos: null as null | PropType<Map<string, LambdaInfo[]>>,
    width: String,
    height: String,
    udid: String
  },
  data() {
    return {
      selectedRequestId: '' as string
    }
  },

  watch: {
    lambdaInfos(value: object) {
      this.selectedRequestId = Object.keys(value)[0]
    }
  },

  methods: {
  }
})
